.map-container {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.toggle {
  -webkit-flex-shrink: 0;
  flex-shrink: 0;
  cursor: pointer;
  color: #93a7bf;
  font-weight: 700;
  padding: 0 12px;
      padding-top: 0px;
      padding-bottom: 0px;
  border-radius: 13px;
  background-color: transparent;
  transition: color .125s,background-color .125s;
}

